.todos-list {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;

  &__item {
    font-size: 18px;
    width: 400px;
    border: 1px solid black;
    margin-bottom: 5px;
    list-style: none;
    border-radius: 5px;
    padding: 20px;

    &--id {
      margin: 0;
      text-align: center;
    }

    &--completed {
      background-color: #90ee90;
    }

    &--not-completed {
      background-color: #ff7f7f;
    }
  }
}
